<template>
  <div>
    <div id = "header">
      <div id = "userIcon">
        <div class = "iconDeck">
          <hr/>
        </div>
        <Tooltip content="点击更换头像" placement="bottom">
        <div id = "icon">
          <div id = "icon-hint" @click = "changeIcon">
          </div>
          <div id = "icon-display">
            <img src="https://i.loli.net/2017/08/21/599a521472424.jpg"/>
          </div>
          
        </div>
        </Tooltip>
        <div class = "iconDeck">
          <hr/>
        </div>
      </div>
      <div id = "userInfo">
        <div>{{name}}</div>
        <div><span><Tooltip content="积分记录" placement="bottom"><Button type="text">当前积分</Button></Tooltip></span><span><Button type="text">开通课堂派vip</Button></span></div>
      </div>
      <div id = "nav"><span><Button type="text">个人设置</Button></span><span><Button type="text">通知设置</Button></span><span><Button type="text">我的文档</Button></span>
      </div>
    </div>
    <div id = "main">
      <transition name="component-fade" mode="out-in">
        <router-view>
        </router-view>
      </transition>
    </div>
    <div>
    <div id = "modalContainer">
      <Modal
        v-model="modalVisible1"
        title="设置头像"
        :loading="modalOKloading"
        @on-ok="changeIconOK">	
        
        <div id = "cropperContainer">
          <div id = "cropper">
            <img id="image" src="https://fengyuanchen.github.io/cropper/images/picture.jpg">
          </div>
          <div id = "cropperPreview">
          </div>
        </div>
      </Modal>
    </div>
    
    </div>
  </div>
</template>

<script>
import userInfo from '@/components/classes/setting_user'
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.min.css'

export default {
  name: 'classes-setting',
  data () {
    return {
      modalOKloading:true,
      modalVisible1:false,
      cropper:{}
    }
  },
  created(){
    this.getUserInfo();
  },
  methods:{
    getUserInfo(){
      var that = this;
      this.$axios.get('getUserInfo.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.$store.commit('setUser',res.data.obj);
        }else{
        }
      })
      .catch(function(e){
      });
    },
    changeIcon(){
      this.modalVisible1 = true;
        var image = document.getElementById('image');
        this.cropper = new Cropper(image, {
          aspectRatio: 1 / 1,
          crop: function(e) {
            console.log(e.detail.x);
            console.log(e.detail.y);
            console.log(e.detail.width);
            console.log(e.detail.height);
            console.log(e.detail.rotate);
            console.log(e.detail.scaleX);
            console.log(e.detail.scaleY);
            console.log(e.detail);
          }
        });
      
    },
    changeIconOK(){
      
    }
    //
  },
  computed:{
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
  },
  userInfo:userInfo
}
</script>

<style scoped lang = "less">

@bor-c:#c8c8c8;


#header {
  background-color: rgba(255, 255, 255, 0.6);
  padding-top:3rem;
  border-top:1px solid @bor-c;
}
#userIcon{
  display:flex;
  .iconDeck {
    flex:8;
    padding-top:3rem;
  }
  #icon {
    flex:2;
    position:relative;
    #icon-hint,#icon-display {
      width:6rem;
      height:6rem;
      margin:0 auto;
      border-radius:100rem;
      }
    #icon-hint {
      position:absolute;
      top:0;
      right:0;
      left:0;
      border-radius:100rem;
      display:flex;
      align-items:center;
      justify-content:center;
      &:hover{
        background-color: rgba(255, 255, 255, 0.6);
        color:black;
        font-weight:bold;
        }
      }
    img {
      border-radius:100rem;
      width:inherit;
      height:inherit;
    }
  }
}
#userInfo {
  padding:1rem 0;
  > div:first-child {
    font-size:1.6rem;
    font-weight:bold;
  }
  > div+div {
    margin-top:.5rem;
    display:inline-block;
    background-color: #f2f2f2;
    padding:.5rem;
    border-radius:.2rem;
    > span+span {
      margin-left:4rem;
    }
  }

}
#nav {
  border-top:1px solid @bor-c;
  border-bottom:1px solid @bor-c;
  display:flex;
  justify-content:center;
  padding:1rem 0;
  > span {
    margin:0 3rem;
  }
}
#main {
  border:1px solid @bor-c;
  min-height:20rem;
  width:70%;
  margin:0 auto;
  margin-top:3rem;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2rem 4rem;

}
/**过渡效果**/
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to
  /* .component-fade-leave-active for below version 2.1.8 */ {
    opacity: 0;
}
/*vue-cropper 效果*/

#cropperContainer {
  display: flex;
  
  
  #cropper {
    flex:5;
    overflow: hidden;
    height: 20rem;
    widows: 20rem;
    
  }
  #cropperPreview {
    flex:3;
  }
}

 

</style>



