<template>
  <div class = "chatting">
    <div style = "padding:.5rem">secret</div>
    <div class = "msgs">
      <div class = "none-default" style = "padding:4rem 0;text-align:center" v-show="msgs.length == 0">没有任何消息</div>
      <div class = "msgs-container">
        <div v-for="item in msgs" :key="item">
        <div class = "msg msg-odd">
          <div class = "main">
            <div><Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" shape="square" size = "large"/></div>
            <div style = "margin-left:1rem;background:#E4E4E4;padding:.5rem;border-radius:5px;max-width:80%;">
                首先： 
               部门与组织是manytoone和onetomany的关系，并且两张表都有自关联，查询部门信息时会级联查询上级部门以及他的组织结构，然后返回json数据时，会出现$ref 
                 即：
            </div>
          </div>
          <div class = "bottom">2017/12/26 16:46</div>
        </div>
        <div class = "msg msg-even">
          <div class = "main">
            <div style = "margin-right:1rem;background:#b2e281;padding:.5rem;border-radius:5px;max-width:80%;">
              首先： 
              部门与组织是manytoone和onetomany的关系，并且两张表都有自关联，查询部门信息时会级联查询上级部门以及他的组织结构，然后返回json数据时，会出现$ref 
              即：
            </div>
            <div><Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size = "large"/></div>
          </div>
          <div class = "bottom">2017/12/26 16:46</div>
        </div>
        </div>
      </div>
    </div>
    <div class = "sendmsg">
      <div style = "margin-bottom:.5rem"><Icon type="paperclip" style = "font-size:2.5rem;margin:0 1rem;"></Icon><span>(附件)</span></div>
      <div>
        <Input v-model="msg" type="textarea" :rows = "4" placeholder="Enter something..." @on-keypress = "preSendMsg"></Input>
      </div>
      <div style = "text-align:right;margin-top:.5rem">
        <span style = "color:#c8c8c8">Ctrl + Enter 发送</span>
        <Button type = "default" size = "large" style = "width:8rem;margin-left:2rem;">发送</Button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'chatting',
  data () {
    return {
      msgs:[1,2],
      msg:'',
      targetuser:null
    }
  },
  created () {
    
  },
  methods: {
    preSendMsg(e){
      if(e.keyCode == 10){
        this.sendMsg();
      }
    },
    sendMsg(){
      var that = this;
      var params = new URLSearchParams();
      params.append('targetuser',this.targetuserId);
      params.append('msg',this.msg); 
      this.$axios.post('saveMsg.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          var msg = {};
          msg.targetuser = that.targetuserId;
        }else{
          alert("出错了，请重试或重新登录。");
        }

      })
      .catch((e)=>{
        console.log(e);

      });
    },
    getMsg(){
      var that = this;
      this.$store.get('getMsg.aurora',{
        params:{
          targetuser:that.targetuserId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){


        }else {


        }

      })
      .catch((e)=>{
        console.log(e);

      });
    },
    startChatting(member){
      this.targetuser = member;
      console.log(this.currentUser.name);
    }
  },
  computed: {
    targetuserId(){
      if(this.targetuser == null){
        return '';
      }else {
        return this.targetuser.id;
      }
    },
    currentUser(){
      return this.$store.getters.getUser;
    }
  }
}
</script>
<style lang="less" scoped>
  @bor-c:#c8c8c8;
  .chatting {
    background: white;
    .msgs {
      border-top:1px solid @bor-c;
      border-bottom:1px solid @bor-c;
      max-height:14rem;
      overflow: auto;
      .msgs-container {
        .msg {
          .main {
            display: flex;
            align-items:center;
          }
          .bottom {
            color:#aaa;
          }
        }
        .msg-odd {
          text-align: left;
          .main {
            justify-content: flex-start;
          }
        }
        .msg-even {
          text-align: right;
          margin-top:.5rem;
          .main {
            justify-content: flex-end;
          }
        }


      }

    }
    .sendmsg {
      
    }

  }

</style>



